.page-header {
  padding: 20px 0;

  background-color: $basic-white;
  border-bottom: 1px solid $basic-light-grey;
}

.page-header__logo {
  width: 22%;

  font-weight: 600;
  font-size: 24px;
  color: $basic-black;
  text-transform: uppercase;
  text-decoration: none;

  & span {
    color: $basic-yellow;
  }

  &:hover {
    color: $basic-black;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.6;
  }
}

.navbar-toggler-icon {
  background-image: url("../img/menu.webp");
  background-repeat: no-repeat;

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.main-nav__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.user-nav {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.user-nav__link--sign-in,
.user-nav__link--sign-up {
  color: $special-grey;
  text-decoration: none;

  &:hover {
    color: $basic-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.user-nav__link--sign-in {
  padding-left: 20px;

  color: $basic-black;

  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    color: $basic-yellow;

    background-image: url("../img/user-hover.svg");
  }
}

.user-nav__link--sign-up {
  padding-left: 25px;

  background-image: url("../img/user-in.svg");
  background-repeat: no-repeat;
  background-position: left center;
}

.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: auto;
}

.user-nav__item {
  margin-right: 20px;
  padding: 10px 15px;

  font-size: 14px;

  background-color: $basic-light-grey;
  border-radius: 5px;

  &:last-child {
    margin-right: 0;
  }
}

.site-list__link {
  margin-right: 30px;
  padding-bottom: 5px;

  font-size: 13px;
  color: $special-grey;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    color: $basic-black;

    border-bottom: 2px solid $basic-yellow;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.navbar-expand-lg {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 1199px) {
  .site-list__link {
    margin-right: 15px;

    font-size: 13px;
  }
}

@media screen and (max-width: 991px) {
  .page-header__logo {
    width: 60%;
  }

  .main-nav__list {
    width: 100%;
  }

  .socials {
    width: 100%;
    margin-top: 30px;
  }

  .user-nav {
    width: 100%;
    margin-top: 40px;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  .user-nav__item {
    margin-bottom: 30px;

    font-size: 16px;
  }

  .site-list__link {
    font-size: 16px;
  }

  .site-list__item {
    margin-bottom: 20px;
  }
}
