.main-screen {
  position: relative;

  padding: 50px 0;
}

.main-screen--wrapper {
  display: flex;
  align-items: center;
  padding-top: 50px;
}

.main-screen__title {
  position: relative;

  font-weight: 600;
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;

    width: 370px;
    height: 2px;

    background-color: $basic-yellow;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;

    width: 370px;
    height: 2px;

    background-color: $basic-yellow;
  }
}

.modal-header .modal-title {
  text-align: center;
}

.main-screen__subtitle {
  margin-top: 30px;

  font-size: 15px;
  line-height: 26px;
  color: $special-black;
}

.main-button {
  display: block;
  width: 200px;
  margin-top: 50px;
  padding: 15px 40px;

  font-size: 14px;
  text-align: center;
  color: $basic-black;
  text-decoration: none;

  background-color: $basic-yellow;
  border-radius: 5px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);

  &:hover {
    color: $special-black;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.prices {
  @include reset-list;
}

.prices__item-text {
  margin-bottom: 10px;
}

.prices__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;

  border-bottom: 1px solid $basic-light-grey;

  &:last-child {
    border-bottom: none;
  }
}

.prices__item-price {
  margin-bottom: 5px;

  font-size: 14px;
  text-align: center;
  color: $special-grey;
}

@media screen and (max-width: 1199px) {
  .main-screen__title {
    &::before {
      width: 300px;
    }

    &::after {
      width: 300px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }
}

@media screen and (max-width: 991px) {
  .main-screen__title {
    &::before {
      width: 200px;
    }

    &::after {
      width: 200px;
    }
  }

  .main-screen {
    padding: 40px 0;
  }

  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }

  .main-button {
    margin-bottom: 50px;
  }
}

@media screen and (max-width: 767px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }

  .main-screen--img {
    display: flex;
    justify-content: center;
  }

  .main-screen__title {
    &::before {
      width: 100px;
    }

    &::after {
      width: 100px;
    }
  }
}

@media screen and (max-width: 540px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .main-screen__title {
    &::before {
      width: 70px;
    }

    &::after {
      width: 70px;
    }
  }
}

@media screen and (max-width: 440px) {
  .main-screen__title {
    &::before {
      width: 50px;
    }

    &::after {
      width: 50px;
    }
  }
}

@media screen and (max-width: 380px) {
  .main-screen__title {
    &::before {
      width: 30px;
    }

    &::after {
      width: 30px;
    }
  }
}
