body {
  min-width: 320px;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.page {
  height: 100%; }

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000; }

.content {
  flex: 1 0 auto; }

.page-header {
  padding: 20px 0;
  background-color: #ffffff;
  border-bottom: 1px solid #eaeaea; }

.page-header__logo {
  width: 22%;
  font-weight: 600;
  font-size: 24px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none; }
  .page-header__logo span {
    color: #e1b97b; }
  .page-header__logo:hover {
    color: #000000;
    opacity: 0.7; }
  .page-header__logo:focus {
    outline: none;
    opacity: 0.6; }

.navbar-toggler-icon {
  background-image: url("../img/menu.webp");
  background-repeat: no-repeat; }
  .navbar-toggler-icon:focus {
    outline: none;
    opacity: 0.8; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.user-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.user-nav__link--sign-in,
.user-nav__link--sign-up {
  color: #706e6e;
  text-decoration: none; }
  .user-nav__link--sign-in:hover,
  .user-nav__link--sign-up:hover {
    color: #000000;
    opacity: 0.8; }
  .user-nav__link--sign-in:focus,
  .user-nav__link--sign-up:focus {
    outline: none;
    opacity: 0.8; }

.user-nav__link--sign-in {
  padding-left: 20px;
  color: #000000;
  background-image: url("../img/user.svg");
  background-repeat: no-repeat;
  background-position: left center; }
  .user-nav__link--sign-in:hover {
    color: #e1b97b;
    background-image: url("../img/user-hover.svg"); }

.user-nav__link--sign-up {
  padding-left: 25px;
  background-image: url("../img/user-in.svg");
  background-repeat: no-repeat;
  background-position: left center; }

.navbar-collapse {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: auto; }

.user-nav__item {
  margin-right: 20px;
  padding: 10px 15px;
  font-size: 14px;
  background-color: #eaeaea;
  border-radius: 5px; }
  .user-nav__item:last-child {
    margin-right: 0; }

.site-list__link {
  margin-right: 30px;
  padding-bottom: 5px;
  font-size: 13px;
  color: #706e6e;
  text-transform: uppercase;
  text-decoration: none; }
  .site-list__link:hover {
    color: #000000;
    border-bottom: 2px solid #e1b97b; }
  .site-list__link:focus {
    outline: none;
    opacity: 0.7; }

.navbar-expand-lg {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

@media screen and (max-width: 1199px) {
  .site-list__link {
    margin-right: 15px;
    font-size: 13px; } }

@media screen and (max-width: 991px) {
  .page-header__logo {
    width: 60%; }
  .main-nav__list {
    width: 100%; }
  .socials {
    width: 100%;
    margin-top: 30px; }
  .user-nav {
    width: 100%;
    margin-top: 40px; }
  .navbar-collapse {
    display: flex;
    flex-direction: column;
    margin-top: 30px; }
  .user-nav__item {
    margin-bottom: 30px;
    font-size: 16px; }
  .site-list__link {
    font-size: 16px; }
  .site-list__item {
    margin-bottom: 20px; } }

.main-screen {
  position: relative;
  padding: 50px 0; }

.main-screen--wrapper {
  display: flex;
  align-items: center;
  padding-top: 50px; }

.main-screen__title {
  position: relative;
  font-weight: 600;
  font-size: 30px;
  text-align: center;
  text-transform: uppercase; }
  .main-screen__title::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 370px;
    height: 2px;
    background-color: #e1b97b; }
  .main-screen__title::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 370px;
    height: 2px;
    background-color: #e1b97b; }

.modal-header .modal-title {
  text-align: center; }

.main-screen__subtitle {
  margin-top: 30px;
  font-size: 15px;
  line-height: 26px;
  color: #3b3b3b; }

.main-button {
  display: block;
  width: 200px;
  margin-top: 50px;
  padding: 15px 40px;
  font-size: 14px;
  text-align: center;
  color: #000000;
  text-decoration: none;
  background-color: #e1b97b;
  border-radius: 5px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1); }
  .main-button:hover {
    color: #3b3b3b;
    opacity: 0.8; }
  .main-button:focus {
    outline: none;
    opacity: 0.8; }

.prices {
  margin: 0;
  padding: 0;
  list-style: none; }

.prices__item-text {
  margin-bottom: 10px; }

.prices__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  border-bottom: 1px solid #eaeaea; }
  .prices__item:last-child {
    border-bottom: none; }

.prices__item-price {
  margin-bottom: 5px;
  font-size: 14px;
  text-align: center;
  color: #706e6e; }

@media screen and (max-width: 1199px) {
  .main-screen__title::before {
    width: 300px; }
  .main-screen__title::after {
    width: 300px; } }

@media screen and (max-width: 1200px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0; } }

@media screen and (max-width: 991px) {
  .main-screen__title::before {
    width: 200px; }
  .main-screen__title::after {
    width: 200px; }
  .main-screen {
    padding: 40px 0; }
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0; }
  .main-button {
    margin-bottom: 50px; } }

@media screen and (max-width: 767px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0; }
  .main-screen--img {
    display: flex;
    justify-content: center; }
  .main-screen__title::before {
    width: 100px; }
  .main-screen__title::after {
    width: 100px; } }

@media screen and (max-width: 540px) {
  .main-screen__title,
  .main-button,
  .main-screen__subtitle {
    margin-left: 0; } }

@media screen and (max-width: 500px) {
  .main-screen__title::before {
    width: 70px; }
  .main-screen__title::after {
    width: 70px; } }

@media screen and (max-width: 440px) {
  .main-screen__title::before {
    width: 50px; }
  .main-screen__title::after {
    width: 50px; } }

@media screen and (max-width: 380px) {
  .main-screen__title::before {
    width: 30px; }
  .main-screen__title::after {
    width: 30px; } }

.page-footer {
  padding: 30px 0;
  background-color: #eaeaea; }

.footer {
  margin: 0;
  padding: 0;
  list-style: none; }

.page-footer__text {
  margin-bottom: 10px;
  font-size: 15px;
  color: #3b3b3b; }
  .page-footer__text a {
    text-decoration: none;
    color: #000000; }
    .page-footer__text a:hover {
      color: #000000;
      opacity: 0.8; }
    .page-footer__text a:focus {
      outline: none;
      opacity: 0.8; }

.page-footer__copyright {
  font-size: 14px;
  text-align: center;
  color: #3b3b3b;
  margin-bottom: 0; }

@media screen and (max-width: 992px) {
  .socials--footer {
    margin-top: 10px; }
  .page-footer__copyright {
    font-size: 13px; } }

@media screen and (max-width: 768px) {
  .footer {
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr; }
  .page-footer__copyright {
    font-size: 15px; }
  .page-footer__text {
    font-size: 16px; } }

@media screen and (max-width: 576px) {
  .footer {
    grid-template-columns: 1fr 1fr; } }

@media screen and (max-width: 400px) {
  .footer {
    grid-template-columns: 1fr; } }

.modal-input {
  width: 60%;
  display: block;
  margin: 0 auto;
  padding: 10px 15px;
  font-size: 14px;
  border: 1px solid #eaeaea;
  border-radius: 5px; }
  .modal-input:hover {
    opacity: 0.7; }
  .modal-input:focus {
    outline: none;
    opacity: 0.7; }

.modal-input-checkbox {
  width: auto; }

.modal-label {
  width: 100%;
  font-size: 14px;
  margin-right: 30px;
  margin-left: 8px; }

.modal-btn {
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  color: #000000;
  background-color: #e1b97b;
  border: none;
  border-radius: 5px; }
  .modal-btn:hover {
    opacity: 0.7; }
  .modal-btn:focus {
    outline: none;
    opacity: 0.7; }

.modal-footer {
  display: flex;
  flex-direction: column; }

.modal-footer__wrapper {
  display: flex;
  align-items: center; }

.modal-link {
  text-align: center;
  margin-right: 10px;
  font-size: 14px;
  color: #3b3b3b;
  text-decoration: none; }
  .modal-link:hover {
    color: #3b3b3b;
    opacity: 0.8; }
  .modal-link:focus {
    outline: none;
    opacity: 0.8; }

.close {
  width: 18px;
  height: 18px;
  background-color: transparent;
  background-image: url("../img/close.png");
  background-repeat: no-repeat;
  background-position: center;
  border: none; }
  .close:hover {
    opacity: 0.7; }
  .close:focus {
    outline: none;
    opacity: 0.7; }
